body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

a.fill-div {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.product {
  width: 100%;
  min-height: 100vh;
  background: #00008b;
  display: flex;
}

.container {
  margin: auto;
  width: 100%;
  max-width: 540px;
}

img.selected {
  width: 300px;
  height: 300px;
  margin: 0 auto;
  border: 1px solid #282c34;
}

img {
  display: block;
  cursor: pointer;
}

img.center {
  display: block;
  margin: 0 auto;
}

footer {
  margin-bottom: -100px;
  margin-top: 20px;
  display: flex;
  align-items: flex-end;
}
#body_main {
  margin-bottom: -125px;
  margin-top: -20px;
}
#misis {
  width: 100px;
  margin-left: 25px;
  margin-bottom: 25px;
}

.center-in {
  display: table;
  margin: 0 auto;
  width: 100%;
  justify-content: center;
}

.main {
  background-color: #ddd;
}
.left {
  border: 1px solid black;
  display: table-cell;
  margin: 0 auto;
}
.middle {
  display: table-cell;
}
.right {
  display: table-cell;
}

.item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
  width: 100%;
  color: #fff;
  background-color: #00008b;
  margin: 0 15px;
  font-size: 200%;
}

.pict-box {
  height: 100px;
  width: 100px;
  border: 4px black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

