.item-carousel {
  min-height: 40rem;
  min-width: 30rem;
  padding: 40px;
  pointer-events: none;
}

.item-carousel img {
  width: 100%;
  height: 100%;
  border-radius: 2rem;
}

.inner-carousel {
  display: flex;
}

.carousel {
  cursor: grab;
  overflow: hidden;
}

.text {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* number of lines to show */
  line-clamp: 5;
  -webkit-box-orient: vertical;
}

.nav-link {
  font-family: "Nunito";
  font-style: normal;
  color: white !important;
}

.next-button {
